// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import axios from 'axios';  //   AJAX 
import VueAxios from 'vue-axios';  //  Vue 
import 'bootstrap';
import Loading from 'vue-loading-overlay'; // 
import 'vue-loading-overlay/dist/vue-loading.css';
import router from './router';
import './bus';
import currencyFilter from './filters/currency';
import timestampFilter from './filters/timestamp';
import VeeValidate from 'vee-validate';
import zhTW from 'vee-validate/dist/locale/zh_tw';
import VueI18n from 'vue-i18n';
import datePicker from 'vue-bootstrap-datetimepicker';
import 'bootstrap/dist/css/bootstrap.css';
import 'pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css';

Vue.use(datePicker);


Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: 'zhTW'
})
Vue.use(VeeValidate, {
  events: 'input|blur', //
  i18n,
  dictionary: {
    zhTW
  }
})

Vue.use(VueAxios, axios);  //   axios

axios.defaults.withCredentials = true; //   axios  Cookies  

Vue.config.productionTip = false;

Vue.component('Loading', Loading);  //  
Vue.filter('currency', currencyFilter);
Vue.filter('timestamp', timestampFilter);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  i18n,
})

 router.beforeEach((to, from, next) => { // 
 console.log('to', to, 'from', from, 'next', next);
  // ...
  if (to.meta.requiresAuth) {   //  api
    const api = 'https://vue-course-api.hexschool.io/api/user/check';
    //const api = `${process.env.APIPATH}/api/user/check` ;
    axios.post(api).then((response) => {  // vue  this
    //console.log(response.data);
    if (response.data.success) {
        next();
    } else {
        next({
        path: '/login',
        });
       }
    });
  } else {   //  next()
    next();
  }
});


