<template>
 <div>
    <loading :active.sync="isLoading"></loading>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <!-- <th width="80"></th> -->
        </tr>
      </thead>
      <tbody>
        <!--key ( eslint )-->
        <tr v-for="(item, key) in Orders" :key="item.id">
          <td>{{item.create_at | timestamp}}</td>
          <td>{{item.user.name}}</td>
          <td>{{item.user.tel}}</td>
          <td>{{item.products.product}}</td>
          <td>{{item.total}}</td>
          <td>
            <span v-if="item.is_paid" class="text-success"></span>
            <span v-else></span>
          </td>
          <!-- <td>
            <div style="display: inline-block;width: 100px;">
              <button class="btn btn-outline-primary btn-sm" @click=""></button>
              <button class="btn btn-outline-primary btn-sm" @click="delModal(item)"></button>
            </div>
          </td> -->
        </tr>
      </tbody>
    </table>
    <!---->
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{'disable': !pagination.has_pre}">
          <a class="page-link" href="#" aria-label="Previous"
            @click.prevent="getOrders(pagination.current_page - 1)">
            <span aria-hidden="true">&laquo;</span>
            <span class="sr-only">Previous</span>
          </a>
        </li>
        <li
          class="page-item" v-for="page in pagination.total_pages" :key="page"
          :class="{'active': pagination.current_page === page}">
          <a class="page-link" href="#" @click.prevent="getOrders(page)">{{ page }}</a>
        </li>
        <li class="page-item">
          <a class="page-link" href="#" aria-label="Next"
            @click.prevent="getOrders(pagination.current_page + 1)">
            <span aria-hidden="true">&raquo;</span>
            <span class="sr-only">Next</span>
          </a>
        </li>
      </ul>
    </nav>

    <!--deleteModal-->
    <div
      class="modal fade"
      id="delOrderModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempOrder.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteOrder"></button>
          </div>
        </div>
      </div>
    </div>
</div>
</template>

<script>
import $ from "jquery"; //  $  import 

export default {
  data() {
    return {
      Orders: [],
      pagination: {},
      deleteId: '',
      tempOrder: {},
      isLoading: false, // 
      status: {
        fileUploading: false
      }
    };
  },
  methods: {
    getOrders(page = 1) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        //console.log(response.data);   // api 
        vm.isLoading = false;
        vm.Orders = response.data.orders;
        vm.pagination = response.data.pagination; // 
        //console.log(vm.Orders);
      });
    },
    deleteOrder() {
      const vm = this;
      //console.log(vm.index);
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${vm.deleteId}`;
      this.$http.delete(api).then(response => {
      });
    },
    openModal(item) {
      //$('#productModal').modal('show'); //   openModal
      // ES6  Object.assign()
      this.tempOrder = Object.assign({}, item); //  this.tempProduct = item
      //$("#OrderModal").modal("show");
    },
    delModal(item) {
      $("#delOrderModal").modal("show");
      this.deleteId = item.id; //  item.id
      //console.log(item.id);
    },
  },
  created() {
    this.getOrders();
  }
};
</script>


