<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <!--   isNew = true -->
      <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="80"></th>
        </tr>
      </thead>
      <tbody>
        <!--key ( eslint )-->
        <tr v-for="(item, key) in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">{{item.origin_price | currency}}</td>
          <td class="text-right">{{item.price | currency}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <div style="display: inline-block;width: 100px;">
              <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)"></button>
              <button class="btn btn-outline-primary btn-sm" @click="delModal(item)"></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{'disable': !pagination.has_pre}">
          <a
            class="page-link"
            href="#"
            aria-label="Previous"
            @click.prevent="getProducts(pagination.current_page - 1)"
          >
            <span aria-hidden="true">&laquo;</span>
            <span class="sr-only">Previous</span>
          </a>
        </li>
        <li
          class="page-item"
          v-for="page in pagination.total_pages"
          :key="page"
          :class="{'active': pagination.current_page === page}"
        >
          <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
        </li>
        <li class="page-item">
          <a
            class="page-link"
            href="#"
            aria-label="Next"
            @click.prevent="getProducts(pagination.current_page + 1)"
          >
            <span aria-hidden="true">&raquo;</span>
            <span class="sr-only">Next</span>
          </a>
        </li>
      </ul>
    </nav>
    <!-- Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div><div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder=""
                  />
                </div>
          
                <div class="form-group">
                  <label for="customFile">
                     
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  :src="tempProduct.imageUrl"
                  alt
                />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder=""
                  />
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      v-model="tempProduct.unit"
                      placeholder=""
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      v-model="tempProduct.price"
                      placeholder=""
                    />
                    
                  </div>
                </div>
                <hr />
                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="tempProduct.description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      id="is_enabled"
                    />
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>   



<script>
import $ from "jquery"; //  $  import 

export default {
  data() {
    return {
      products: [],
      pagination: {},
      tempProduct: {}, // modal 
      isNew: false,
      deleteId: "",
      isLoading: false, // 
      status: {
        fileUploading: false
      }
    };
  },
  methods: {
    getProducts(page = 1) {
      // ES6 
      //const api = 'https://vue-course-api.hexschool.io/api/jw310/products';
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      //console.log(process.env.APIPATH, process.env.CUSTOMPATH);  // 
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        // vue 
        //console.log(response.data);   // api 
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination; // 
        //console.log(vm.products);
      });
    },
    openModal(isNew, item) {
      //$('#productModal').modal('show'); //   openModal
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        // ES6  Object.assign()
        this.tempProduct = Object.assign({}, item); //  this.tempProduct = item
        this.isNew = false;
      }
      $("#productModal").modal("show");
    },
    updateProduct() {
      //updata API
      //let api = 'https://vue-course-api.hexschool.io/api/jw310/admin/product';
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      let httpMethod = "post";
      const vm = this;
      //console.log(process.env.APIPATH, process.env.CUSTOMPATH);  // 
      // { data: vm.tempProduct } 
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = "put";
      }
      this.$http[httpMethod](api, { data: vm.tempProduct }).then(response => {
        //console.log(response.data);
        if (response.data.success) {
          $("#productModal").modal("hide"); //  modal
          vm.getProducts();
        } else {
          $("#productModal").modal("hide"); //  modal
          vm.getProducts();
          console.log("");
        }
      });
    },
    delModal(item) {
      $("#delProductModal").modal("show");
      this.deleteId = item.id; //  item.id
      //console.log(item.id);
    },
    deleteProduct() {
      const vm = this;
      //console.log(vm.index);
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.deleteId}`;
      this.$http.delete(api).then(response => {
        //console.log(response.data);
        if (response.data.success) {
          $("#delProductModal").modal("hide"); //  modal
          console.log("");
          vm.getProducts();
        } else {
          $("#delProductModal").modal("hide"); //  modal
          vm.getProducts();
          console.log("");
        }
      });
    },
    uploadFile() {
      // 
      //console.log(this);
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData(); //  
      formData.append("file-to-upload", uploadedFile);
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      vm.status.fileUploading = true;
      this.$http
        .post(url, formData, {
          headers: {
            "content-Type": "multipart/form-data" //  form-data
          }
        })
        .then(response => {
          //console.log(response.data);
          vm.status.fileUploading = false;
          if (response.data.success) {
            // vm.tempProduct.imageUrl = response.data.imageUrl; // 
            // console.log(vm.tempProduct.imageUrl);
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl); // 
          } else {
            this.$bus.$emit("message:push", response.data.message, "danger"); // 
          }
        });
    }
  },
  created() {
    this.getProducts();
  }
};
</script>



